<template>
	<div id="safeSupervise" v-title data-title="安全监管">
		<arcgismap-list :showLandType="'safe'" :showList="false" :data-target-flag="dataTargetFlag" :delete-aida-land="true" :isShowGreenLand="true"></arcgismap-list>
		<safeSuperviseLeft :areaData="areaData" ref="safeSuperviseLeft"></safeSuperviseLeft>
		<safeSuperviseCenter></safeSuperviseCenter>
		<safeSuperviseRight :areaData="areaData" @click="handleClick" ref="safeSuperviseRight"></safeSuperviseRight>

		<lawSuperviseDialog :regulatoryVisible.sync="regulatoryVisible" :patrolRecordId="patrolRecordId" :title="dialogTitle" v-if="regulatoryVisible"></lawSuperviseDialog>
	</div>
</template>
<script>
import {getStorage} from '@/utils/storage';
import arcgismapList from '@/components/map/arcgismap-list.vue';

import {areaData} from '@/common/config/filterArea.js';

export default {
	name: 'safeSupervise',
	data() {
		return {
			areaData: areaData,
			regulatoryVisible: false,
			patrolRecordId: '',
			dialogTitle: '',
			dataTargetFlag: 0, // 数据源 0神农口袋 1种植系统
		};
	},
	created() {},
	async mounted() {
		let that = this;
		var subjectVo = getStorage('govGovernmentSubjectVO');
		this.dataTargetFlag = Number(subjectVo.dataTargetFlag);
		function debounce(fn, delay) {
			let timer = null;
			return function () {
				const context = this;
				const args = arguments;
				clearTimeout(timer);
				timer = setTimeout(function () {
					fn.apply(context, args);
				}, delay);
			};
		}
		function resizeHander() {
			that.$refs.safeSuperviseLeft.refreshRender();
			that.$refs.safeSuperviseRight.refreshRender();
			that.$refs.safeSuperviseRight.refresh();
		}
		window.resizefn = debounce(resizeHander, 200);

		window.removeEventListener('resize', window.resizefn, false);
		window.addEventListener('resize', window.resizefn, false);
	},
	methods: {
		handleClick(row) {
			this.regulatoryVisible = true;
			this.patrolRecordId = row.patrolRecordID;
			this.dialogTitle = row.cname;
		},
	},
	destroyed() {
		window.removeEventListener('resize', window.resizefn, false);
	},
	components: {
		arcgismapList,
		safeSuperviseLeft: () => import('./safeSupervise-left.vue'),
		safeSuperviseRight: () => import('./safeSupervise-right.vue'),
		safeSuperviseCenter: () => import('./safeSupervise-center.vue'),
		lawSuperviseDialog: () => import('@/components/overview/law-supervise-dialog.vue'),
	},
};
</script>
<style scoped lang="scss">
#safeSupervise {
	position: relative;
}
/deep/ .map-view #gismap_nongyongdi img {
	filter: hue-rotate(140deg) brightness(1.8);
	-webkit-filter: hue-rotate(82deg) brightness(58) saturate(0);
	opacity: 0.1;
}
</style>



